import { DeepPartial, LocalesMock } from '../core'
import { VaultType } from './vault-type.model'

/**
 * Vault Type Mock
 */
export function VaultTypeMock(options?: DeepPartial<VaultType>): VaultType {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'VAULT_TYPE_ID',
    code: options?.code || 'VAULT_TYPE_CODE',
    name: LocalesMock(options?.name),
    description: LocalesMock(options?.description),
    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
